#phone {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
}

// ---------------------------------------------------------------------
//
//                             MINIMIZED
//
// ---------------------------------------------------------------------

#small-phone-wrapper {
  position: absolute;
  bottom: 0;
  right: 0;
  margin-right: 10vw;
  width: 15vw;
  max-width: 200px;
  // margin-bottom: 2vh;
}

#small-phone-wrapper.maximized {
  pointer-events: none;
}

#small-phone-inner-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

#small-phone-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  transition: 200ms ease-in-out;
  transform: translateY(50%);
  cursor: pointer;
}

#small-phone-content img,
#small-phone-content svg {
  pointer-events: all;
  width: 100%;
}

#small-phone-content:hover {
  pointer-events: all;
  transform: translateY(40%);
}

#small-phone-wrapper.maximized #small-phone-content {
  transform: translateY(100%);
}

#small-phone-floating-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #59316d;
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-align: center;
  padding: 0 10px 10px 10px;
  transition: 100ms ease-in-out;
  opacity: 0;
  min-width: 200px;
  position: absolute;
  transform: translateY(-100%);
  border-radius: 1rem;
}

#small-phone-content:hover #small-phone-floating-text {
  opacity: 1;
  background-color: #ffffffdd;
}

#small-phone-floating-text span[lang="pt-br"] {
  font-family: "Barlow";
  font-weight: bold;
}

#small-phone-floating-text span[lang="default"]::before {
  content: "";
  display: block;
  position: relative;
  width: 50%;
  margin: 10px auto;
  border-top: 2px solid #f9afa1;
}

#small-phone-floating-text span[lang="default"] {
  font-family: "Barlow";
  font-style: italic;
}

